import React from 'react';
import { Switch, Route } from 'react-router-dom';
import asyncComponent from './Utils/AsyncComponent';

const asyncMain = asyncComponent(() => import('./components/MainPage'));
const asyncPost = asyncComponent(() => import('./components/Post/Post'));

export default ({childProps}) =>
  <div className="App">
    <Switch>
      <Route exact path='/Blog' component={asyncMain} props={childProps} />
      <Route path='/post/:id' component={asyncPost} props={childProps} />
    </Switch>
  </div>



