import { takeEvery, takeLatest, call, put } from 'redux-saga/effects';
import axios from 'axios';
import * as actionType from '../Const/const';

export function* watcherSaga() {
    yield takeEvery(actionType.API_CALL_REQUEST, mainSaga)
}

function fetchMainData() {
    return axios({
        method: 'get',
        url: 'https://jsonplaceholder.typicode.com/posts'
    });
}

function* mainSaga() {
    try {
        const response = yield call(fetchMainData);
        yield put({type: actionType.API_CALL_SUCCES, response});
    } catch (e) {
        yield put({type: actionType.API_CALL_FAILURE, e});
    }
}



