import * as actionType from '../Const/const';

const initialState = {
    ListItem: [],
    PostItem: [],
    ComData: [],
    fetching: false,
    error: false
}

const reducer = (state = initialState, action) => {
    switch(action.type) {
        case actionType.API_CALL_REQUEST:
        return {
            ...state,
            fetching: true,
            error: false
        }
        case actionType.API_CALL_SUCCES:
        return {
            ...state,
            ListItem: action.response.data,
            fetching: false,
            error: false
        }
        case actionType.API_CALL_FAILURE:
        return {
            ...state,
            fetching: false,
            error: true
        }
        case actionType.RECEIVE_ITEM:
        return {
            ...state,
            PostItem: action.payload
        }
        default:
            return {
                ...state
            };
    }
}

export default reducer;


